.offers {
  display: flex;
  flex-wrap: wrap;
}

.offers__info {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.offers__title {
  position: relative;
  font-style: normal;
  font-weight: 600;
  font-size: 55px;
  line-height: 80px;
  color: #2c2e3f;
  width: 33%;
  margin-left: 15px;
  letter-spacing: -1.245px;

  margin-left: 50px;
}

.offers__title::before {
  content: "";

  position: absolute;
  bottom: -31px;
  left: 0px;

  width: 228.51px;
  height: 4px;

  background-color: #fe7865;
}

.offers__description {
  width: 33%;
  font-weight: normal;
  font-size: 16px;
  line-height: 28px;
  margin-left: 50px;
  margin-bottom: 55px;
}

.offers__list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-around;

  width: 100%;
}

.offers__item {
  position: relative;
  margin-bottom: 100px;
}

.offers__name {
  position: absolute;
  bottom: 4px;
  padding: 16px 16px 16px 16px;
  right: calc(50% - 125px);
  min-width: 215px;

  font-weight: 500;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  letter-spacing: -0.415px;

  background-color: #ffffff;

  z-index: 3;
}

.offers__shape {
  display: none;
  position: absolute;
  top: 0px;
  width: 350px;
  height: 430px;
  background-color: rgba(20, 21, 23, 0.6);
  z-index: 1;
}

.offers__item:hover .offers__shape{
  display: block;
}

.offers__button {
  position: absolute;
  top: 40%;
  left: 15%;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  padding: 30px 20px 30px 20px;
  border: 1px solid rgba(255, 255, 255, 0.7);
  box-sizing: border-box;

  text-align: center;
  letter-spacing: 2px;
  text-transform: uppercase;
  text-decoration: none;

  color: #ffffff;
}

@media (max-width: 1024px) {
  .offers__info {
    justify-content: center;
  }

  .offers__title {
    width: 90%;
    box-sizing: padding-box;

    font-style: normal;
    font-weight: 600;
    font-size: 30px;
    line-height: 150%;

    text-align: center;

    margin-right: 50px;
  }

  .offers__title::before {
    display: none;
  }

  .offers__description {
    width: 40%;
    margin-right: 50px;
    text-align: center;
    align-self: center;
  }
}
