.more-offers {
  position: relative;
}

.more-offers::before {
  position: absolute;
  content: "";

  height: 189px;
  width: 1px;
  right: 0px;
  top: 20px;
  background-color: #d7d9df;
}

.more-offers__wrapper {
  display: flex;
  flex-direction: row;
}

.more-offers__title {
  margin-bottom: 25px;
}

.more-offers__list {
  list-style: none;
  padding: 0;
  margin: 0;
  margin-right: 50px;
}

.more-offers__item {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  color: rgba(20, 21, 23, 0.5);
  margin-bottom: 10px;
}

@media (max-width: 1024px) {
  .more-offers__list {
    margin-right: 30px;
  }
}
